import React from "react";
// import react pro sidebar
import { ProSidebar, Menu, SidebarContent, MenuItem } from "react-pro-sidebar";
// history import
import { useNavigate, useLocation } from "react-router-dom";
// import "react-pro-sidebar/dist/css/styles.css";    
import "./style.scss";

const Sidebar = ({navbarStatus}) => {
  const navigate = useNavigate();
  const location = useLocation();
  
  return (
    <React.Fragment>
      <ProSidebar className={`${navbarStatus ?  "active_sidebar" : "react_pro_sidebar"}`}>
        <SidebarContent className="react_pro_sidebar--content">
          <Menu>
            <MenuItem
              className={`${location.pathname.includes("dashboard/driver")
                  ? "active_menu"
                  : "inactive_menu"
                } inner_item`}
              onClick={() => {
                if (!"dashboard/driver".includes(location.pathname)) {
                  navigate("/dashboard/driver");
                }
              }}
            >
              Drivers
            </MenuItem>
            {/* <MenuItem>Routes</MenuItem>
            <MenuItem>Customers</MenuItem> */}
            <MenuItem
              className="inactive_menu inner_item"
              onClick={() => {
                // clear local storage
                localStorage.clear();
                navigate("/");
              }}
            >
              Logout
            </MenuItem>
          </Menu>
        </SidebarContent>
      </ProSidebar>
    </React.Fragment>
  );
};

export default Sidebar;
