import React, { useState } from "react";
// import react router
import { useNavigate } from "react-router-dom";
// import css file
import "./style.scss";

const Navbar = ({ setnavbarStatus }) => {
  const navigate = useNavigate();
  const [sidebaractive, setSidebaractive] = useState(false);

  return (
    <React.Fragment>
      <div className="Navbar">
        <div className="Navbar__container">
          <div className="Navbar__container--logo--sidebar"  >
            <div style={{ cursor: "pointer" }}>
              {!sidebaractive ?
                <img style={{ width: "30px", height: "30px", padding: "5px" }}
                  src="/assets/image/sidebar.png"
                  onClick={() => (setSidebaractive(true),
                    setnavbarStatus(true))}
                >
                </img> :
                <img style={{ width: "30px", height: "30px", padding: "6px" }}
                  src="/assets/image/close.png"
                  onClick={() => (setSidebaractive(false), setnavbarStatus(false))}
                >
                </img>
              }
            </div>
          </div>
          <div className="Navbar__container--logo">
            <div >
              <h2
                className="m-0"
                onClick={() => {
                  navigate("/dashboard/driver");
                }}
              >
                <img className="Navbar__container--logo--img"
                  src="/assets/image/logo-small-callahead.png"
                >
                </img>
              </h2>
            </div>
          </div>
          <div style={{ display: 'flex', justifyContent: 'end', width: '33.33%' }}>
            <div className="Navbar__container--info">
              <p className="m-0">Super Admin</p>
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

export default Navbar;